import React, { Component } from 'react'
import $ from 'jquery'
import './App.css'
import TodoInput from './TodoInput'
import TodoItem from './TodoItem'
import Scrollbar from './Scrollbar'
import 'normalize.css'
import './reset.css'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      newTodo: '',
      todoList: [
        {
          id: '1',
          content: 'localStorage',
          status: 'undone'
        },
        {
          id: '2',
          content: 'leanCloud',
          status: 'undone'
        },
        {
          id: '3',
          content: '',
          status: 'undone'
        },
        {
          id: '4',
          content: 'UP ',
          status: 'undone'
        },
        {
          id: '5',
          content: 'DOWN ',
          status: 'undone'
        },
        {
          id: '6',
          content: 'Beautify it!',
          status: 'undone'
        },
      ]
    }
  }
  render() {
    let todos = this.state.todoList
      .filter((item) => item.status !== 'delete')
      .map((item) => {
        return <TodoItem todo={item}
          onToggle={this.changeItemStatus.bind(this)}
          onTop={this.toTop.bind(this)}
          onUp={this.moveUp.bind(this)}
          onDown={this.moveDown.bind(this)}
          onBottom={this.toBottom.bind(this)}
          onDelete={this.deleteItem.bind(this)}
        />
      })

    return (
      <div className='App'>
        <header className='todo-header'>
          <h1 className='todo-title'></h1>
        </header>
        <div className='todo-list'>
          <Scrollbar />
          <ul>{todos}</ul>
        </div>
        <TodoInput className='todo-inputWrapper' id='add'
          content={this.state.newTodo}
          onSubmit={this.addItem.bind(this)}
          onChange={this.changeNewtodo.bind(this)}
        />
      </div>
    )
  }
  componentDidMount() {
    this.showScroll()
  }
  componentDidUpdate() {
    this.showScroll()
  }
  /* *************** */
  showScroll() {
    let contentHeight = $('.todo-list ul').outerHeight(true),
      scrollTop = undefined
    if (contentHeight > 440) {
      $('.scrollWrapper').css({ display: 'block' })
      $('.scrollBar').css({ height: `${440 * 440 / contentHeight}px` })
      $('.todo-list').scroll(function () {
        scrollTop = $('.todo-list').scrollTop()
        $('.scrollBar').css({ top: `${scrollTop / contentHeight * 100}%` })
      })
    } else {
      $('.scrollWrapper').css({ display: 'none' })
    }
  }
  addItem() {
    this.state.todoList.push({
      id: 5,
      content: this.state.newTodo,
      status: 'undone'
    })
    this.setState({
      newTodo: '',
      todoList: this.state.todoList
    })
  }
  changeNewtodo(value) {
    this.setState({
      newTodo: value,
      todoList: this.state.todoList
    })
  }
  changeItemStatus(eventTarget, todoTarget) {
    if (todoTarget.todo.status === 'undone') {
      todoTarget.todo.status = 'done'
      $(eventTarget.children[0])[0].src = todoTarget.done
      $(eventTarget.children[1]).addClass('done')
    } else {
      todoTarget.todo.status = 'undone'
      $(eventTarget.children[0])[0].src = todoTarget.undone
      $(eventTarget.children[1]).removeClass('done')
    }
    this.setState(this.state)
  }
  toTop(eventTarget) {
    let index = $(eventTarget).index()
    if (index === 0) {
      alert('')
    } else {
      $('li').eq(0).before($(eventTarget))
    }
  }
  moveUp(eventTarget) {
    let index = $(eventTarget).index()
    if (index === 0) {
      alert('')
    } else {
      $('li').eq(index - 1).before($(eventTarget))
    }
  }
  moveDown(eventTarget) {
    let index = $(eventTarget).index(), lastIndex = $(eventTarget).parent().children().length - 1
    if (index === lastIndex) {
      alert('')
    } else {
      $('li').eq(index + 1).after($(eventTarget))
    }
  }
  toBottom(eventTarget) {
    let index = $(eventTarget).index(), lastIndex = $(eventTarget).parent().children().length - 1
    if (index === lastIndex) {
      alert('')
    } else {
      $('li').eq(lastIndex).after($(eventTarget))
    }
  }
  deleteItem(todoTarget) {
    todoTarget.status = 'delete'
    this.setState(this.state)
  }
}

export default App



